* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	user-select: none;
}
#div-app {
	width: 100%;
	height: 100vh;
	overflow: hidden;
}

table {
	height: 100%;
	width: 100%;
	border-collapse: collapse;
}

td {
	height: 16.66%;
	width: 25%;
	color: black;
	background-color: #eee;
	font-size: 3.5rem;
	text-align: center;
}

table tr:first-child td {
	background-color: #858694;
	color: white;
	text-align: right;
	padding: 0 40px;
	font-size: 6.5rem;
	line-height: 6.5rem;
}

table tr > td:last-child:not(:first-child) {
	background-color: #f5923e;
	color: white;
}

table tr:not(:first-child) td {
	border: 1px solid #858694;
}
